@font-face {
  font-family: "MontserratBold";
  src: url("./fonts/Monrseratt-Bold/Montserrat-Bold.eot") format("eot"), url("./fonts/Monrseratt-Bold/Montserrat-Bold.otf") format("otf"), url("./fonts/Monrseratt-Bold/Montserrat-Bold.svg") format("svg"), url("./fonts/Monrseratt-Bold/Montserrat-Bold.woff") format("woff"), url("./fonts/Monrseratt-Bold/Montserrat-Bold.ttf") format("ttf");
}
@font-face {
  font-family: "MontserratRegular";
  src: url("./fonts/Monrseratt-Regular/Montserrat-Regular.otf") format("otf"), url("./fonts/Monrseratt-Regular/Montserrat-Regular.svg") format("svg"), url("./fonts/Monrseratt-Regular/Montserrat-Regular.eot") format("eot"), url("./fonts/Monrseratt-Regular/Montserrat-Regular.woff") format("woff"), url("./fonts/Monrseratt-Regular/Montserrat-Regular.ttf") format("ttf");
}
body {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

* {
  box-sizing: border-box;
}

h1, h2, h3 {
  font-family: "MontserratBold";
  padding: 0;
  margin: 0;
  color: #1F2041;
}

h1 {
  font-size: 1.5rem;
}

h2 {
  font-size: 1.1875rem;
}

h3 {
  font-size: 0.75rem;
}

body {
  padding: 0 15px;
}

/*----------- litle blocks ---------------
//----------- text-blocks  --------------*/
.text {
  display: flex;
  flex-direction: column;
  max-width: 320px;
}
.text label {
  text-transform: uppercase;
}

.text__global {
  border: 1px solid #C7C7CF;
  border-radius: 0.5em;
  outline: none;
  font-size: 0.875rem;
  margin-top: 0.5em;
  padding: 1em 0;
  padding-left: 1em;
  width: 100%;
}

.text__global_hover-focus:hover {
  border-color: #8F90A0;
}
.text__global_hover-focus:focus {
  border-color: #8F90A0;
}
.text__global_hover-focus:focus::placeholder {
  opacity: 0;
}

.text__wrapper-block {
  position: relative;
}

.text__arrow-right {
  height: 20px;
  width: 40px;
  position: absolute;
  right: 10px;
  right: 10px;
  top: 22px;
}
.text__arrow-right::after {
  content: " ";
  position: absolute;
  top: 3px;
  border: solid #8BA4F9;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 4px;
  transform: rotate(-45deg);
  right: 10px;
}
.text__arrow-right::before {
  content: " ";
  position: absolute;
  top: 7px;
  right: 0;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  background-color: #8BA4F9;
  width: 15px;
  height: 3px;
  display: inline-block;
}
.text__arrow-right:hover, .text__arrow-right :focus {
  cursor: pointer;
}
.text__arrow-right:hover::before, .text__arrow-right:hover::after, .text__arrow-right :focus::before, .text__arrow-right :focus::after {
  opacity: 0.5;
}

.text__subscribe-email_width {
  max-width: 267px;
}

/*----------- text-blocks  --------------*/
/*----------- drop-downs-blocks  --------------*/
.drop-downs__block-general {
  display: none;
  flex-direction: column;
  max-width: 320px;
  padding-left: 0.6em;
  padding-right: 0.4em;
  padding-bottom: 0.6em;
  border: 1px solid #C7C7CF;
}

.drop-downs__block-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0.6em 0;
}
.drop-downs__block-row span {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #8F90A0;
  height: 30px;
  width: 30px;
  border: 1px solid #8F90A0;
  border-radius: 50%;
  user-select: none;
}
.drop-downs__block-row span:hover {
  cursor: pointer;
}
.drop-downs__block-row span:active {
  color: #575871;
  border-color: #575871;
}

.drop-downs__score-minus_no-active {
  opacity: 0.5;
}

.drop-downs__score {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 100px;
  width: 100%;
}

.drop-downs__buttons-guest {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin-top: 0.6em;
}

.drop-downs__general_width {
  max-width: 266px;
}

.drop-downs {
  display: flex;
  flex-direction: column;
  max-width: 320px;
}

.drop-downs__global {
  height: 44px;
  width: 100%;
  border: 1px solid #C7C7CF;
  border-top-left-radius: 0.5em;
  border-top-right-radius: 0.5em;
  padding-left: 1em;
  margin-top: 0.5em;
  color: #575871;
  font-family: "MontserratRegular";
  position: relative;
}

.drop-downs__global-arrow {
  display: inline-block;
  height: 20px;
  width: 20px;
  position: absolute;
  top: 38%;
  right: 0;
}
.drop-downs__global-arrow::after {
  content: " ";
  border: 1px solid #575871;
  border-width: 0 2px 2px 0;
  height: 5px;
  width: 5px;
  display: block;
  transform: rotate(45deg);
}
.drop-downs__global-arrow:hover {
  cursor: pointer;
}

.drop-downs__global-text {
  font-size: 0.875rem;
  margin: 0;
  height: 100%;
  display: flex;
  align-items: center;
}

.drop-downs__global-heading {
  text-transform: uppercase;
}

.drop-downs__buttons-item {
  color: #BC9CFF;
}

.drop-downs__buttons-item:first-child {
  display: none;
}

.drop-downs__buttons-item_hover:hover {
  opacity: 0.5;
  cursor: pointer;
}

.calendar {
  height: 369px;
}

