  position: absolute;
  right: 10px;
  right: 10px;
  top: 22px;
}
.calendar__arrow-right::after {
  content: " ";
  position: absolute;
  top: 3px;
  border: solid #BC9CFF;
  border-width: 0 3px 3px 0;
