}
.calendar__arrow-right:hover, .calendar__arrow-right :focus {
  cursor: pointer;
}
.calendar__arrow-right:hover::before, .calendar__arrow-right:hover::after, .calendar__arrow-right :focus::before, .calendar__arrow-right :focus::after {
  opacity: 0.5;
}

.calendar__arrow-left {
  height: 20px;
  width: 40px;
  position: absolute;
  right: 10px;
  left: 10px;
  top: 22px;
}
.calendar__arrow-left::after {
  content: " ";
  position: absolute;
  top: 3px;
  border: solid #BC9CFF;
  border-width: 0 3px 3px 0;
  display: inline-block;
