  display: inline-block;
}
.calendar__arrow-left:hover, .calendar__arrow-left :focus {
  cursor: pointer;
}
.calendar__arrow-left:hover::before, .calendar__arrow-left:hover::after, .calendar__arrow-left :focus::before, .calendar__arrow-left :focus::after {
  opacity: 0.5;
}

.calendar__week {
  display: flex;
  justify-content: space-between;
  max-width: 255px;
  margin: 0 auto;
  margin-top: 50px;
}

.calendar__week-item {
  color: #BC9CFF;
  font-size: 0.75rem;
  font-family: "MontserratBold";
}

.calednar__days {
  margin: 0 auto;
  padding-top: 30px;
  max-width: 255px;
  display: grid;
  grid-template-columns: repeat(7, 15px);
  grid-template-rows: repeat(5, 30px);
  justify-content: space-between;
}

.calednar__days-item {
  display: flex;
  justify-content: center;
  font-size: 0.75rem;
  font-family: "MontserratRegular";
  color: #8F90A0;
}

.calendar__footer {
  max-width: 280px;
  margin: 0 auto;
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
}

.calendar__buttons-item {
  color: #BC9CFF;
}

.calendar__buttons-item_hover:hover {
  opacity: 0.5;
  cursor: pointer;
}

